@import url("https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css");
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Rubik:500,700,900&display=swap');
body {
    margin: 0;
    line-height: 1.5;
    font-size: 16px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400; }
* {
    margin: 0;
    padding: 0;
    outline: none;
    text-decoration: none;
    box-sizing: border-box; }
::before,::after {
    box-sizing: border-box; }
.section {
    background-color: #f2f2fc;
    min-height: 100vh;
    display: block;
    padding: 0 30px;
    .container {
        padding-top: 60px;
        padding-bottom: 70px; } }

.section-title {
    flex: 0 0 100%;
    -ms-flex: 0 0 100%;
    max-width: 100%;
    h2 {
        font-size: 40px;
        color: #302e43;
        font-family: 'Rubik', sans-serif;
        font-weight: 700;
        margin: 0;
        position: relative;
        &::before {
            content: '';
            height: 4px;
            width: 50px;
            position: absolute;
            top: 100%;
            left: 0; }
        &::after {
            content: '';
            height: 4px;
            width: 25px;
            position: absolute;
            top: 100%;
            left: 0;
            margin-top: 8px; } } }

.row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
    position: relative; }

.padd-15 {
    padding-right: 15px;
    padding-left: 15px; }
.main-content {
    padding-left: 270px; }

.container {
    max-width: 1170px;
    width: 100%;
    margin: auto; }
.shoadow-dark {
    -webkit-box-shadow: 0 0 20px rgba(48,46,77,0.15);
    box-shadow: 0 0 20px rgba(48,46,77,0.15); }
.hidden {
    display: none!important; }
// Aside
.aside {
    width: 270px;
    background-color: #fdf9ff;
    position: fixed;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 10;
    border-right: 1px solid #e8dfec;
    padding: 30px;
    & .logo {
        padding: 30px 0;
        & a {
            font-size: 40px;
            color: #302e43;
            font-family: 'Rubik', sans-serif;
            font-weight: 700;
            text-decoration: none;
            display: inline-block;
            position: relative;
            padding: 0 10px;
            &::before {
                content: '';
                height: 20px;
                width: 20px;
                position: absolute;
                top: 0;
                left: 0; }

            &::after {
                content: '';
                height: 20px;
                width: 20px;
                position: absolute;
                bottom: 0;
                right: 0; } } }
    & .nav-toggler {
        height: 40px;
        width: 45px;
        border: 1px solid #d4d4e3;
        cursor: pointer;
        position: fixed;
        left: 300px;
        top: 30px;
        z-index: 11;
        border-radius: 5px;
        background-color: #fdf9ff;
        // display: -ms-flexbox
        // display: flex
        display: none;
        -ms-flex-align: center;
        align-items: center;
        -ms-flex-pack: center;
        justify-content: center;
        & span {
            height: 2px;
            width: 18px;
            display: inline-block;
            position: relative; }
        & span::before {
            content: '';
            height: 2px;
            width: 18px;
            position: absolute;
            top: -6px;
            left: 0; }
        & span::after {
            content: '';
            height: 2px;
            width: 18px;
            position: absolute;
            top: 6px;
            left: 0; } }

    & .nav {
        list-style: none;
        margin: 80px 0;
        padding: 0;
        & li {
            display: block;
            & a {
                font-size: 26px;
                font-weight: 600;
                color: #302e4d;
                text-decoration: none;
                line-height: 70px;
                display: block;
                border-bottom: 1px solid #e8dfec;
                -webkit-transition: all 0.3s ease;
                transition: all 0.3s ease;
                text-transform: capitalize;
                & .fa {
                    margin-right: 10px; } }
            &:not(.active):hover {
                padding-left: 5px; } } }

    & .copyright-text {
        background-color: #ededed;
        font-size: 13px;
        color: #7d7d7d; } }

// Home 16.26

.home {
    min-height: 100vh;
    display: -ms-flexbox;
    display: flex;
    .intro {
        text-align: center;
        img {
            height: 200px;
            width: 200px;
            border-radius: 50%;
            display: inline-block;
            border: 8px solid #ffffff; }
        h1 {
            font-size: 30px;
            color: #302e43;
            font-family: 'Rubik', sans-serif;
            font-weight: 700;
            margin: 20px 0 5px; }
        p {
            font-size: 16px;
            font-weight: 500;
            margin: 0;
            line-height: 22px;
            color: #504e70; }
        .social-links {
            margin-top: 25px;
            a {
                height: 35px;
                width: 35px;
                display: inline-block;
                text-align: center;
                line-height: 35px;
                color: #ffffff;
                margin: 0 4px;
                border-radius: 50%;
                text-decoration: none;
                -webkit-transition: all 0.3s ease;
                transition: all 0.3s ease; }
            a:hover {
                -webkit-transform: scale(1.1);
                transform: scale(1.1); } } } }

